<template>
    <transition
    name="expand"
    @enter= 'enter'
    @after-enter= 'afterEnter'
    @leave= 'leave'>
        <div class="sub-items" v-show="list.open" @click="list.sub.open = !list.sub.open">
            <li class="sub-items-text" v-for="(item, index) in list.sub" :key="index">
                <p>{{item.name}}</p>
                <p>{{item.sets}}</p>
            </li>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'app-dropdown',
    props:['list'],
    methods:{
        enter(el){
            el.style.height = 'auto';
            const height = getComputedStyle(el).height;
            el.style.height = 0;
            getComputedStyle(el);
            setTimeout(() => {
                el.style.height = height;
            });
        },
        afterEnter(el){
            el.style.height = 'auto';
        },
        leave(el){
            el.style.height = getComputedStyle(el).height;
            getComputedStyle(el);
            setTimeout(() => {
                el.style.height = 0;
            });
        }
    }
}
</script>

<style lang="scss" scoped>

    .sub-items{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        top: 0;
        right: 0;
        width: 100%;
        background-color: var(--div-back2);
        margin-block-start: 0;
        margin-block-end: 0;
        padding-inline-start: 0;
        padding: 10px;
        li:not(:last-child){
            border-bottom: 1px solid rgba($color: #000000, $alpha: .4);
        }
        &-text{
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }
    .expand-enter-active, .expand-leave-active{
        transition: height .5s ease-in-out;
        overflow: hidden;
    }
</style>