

const themes = {
  dark: {
    'nav-color': '#1f1f1f',
    'background-color': '#121212',
    'primary-color': 'rgb(241, 0, 55)',
    'secondary-color': 'rgb(187, 0, 44)',
    'font-color': 'whitesmoke',
    'div-back' : '#1d1d1d',
    'div-back2' : '#1f1f1f',
    'pic-gradient': 'rgba(0, 0, 0, .6)'
  },
  light: {
    'nav-color': '#fff',
    'background-color': 'white',
    'primary-color': 'rgb(36, 92, 168)',
    'secondary-color': 'rgb(29, 72, 131)',
    'font-color': 'rgb(17, 17, 17)',
    'div-back' : '#d8d7d7',
    'div-back2' : 'rgb(182, 181, 181)',
    'pic-gradient': 'rgba(0, 0, 0, .3)'
  },
  cascoTheme: {
    'primary': '#f16820',
    'background-color': '#1a3264',
    'text-color': '#e8e2da'
  },
  tealTheme: {
    'primary-color': '#ff4081',
    'background-color': '#00796b',
    'text-color': '#ffffff'
  }
};

export const ThemingService = {
  changeTheme(themeVal) {
    this.setTheme(themes[themeVal]);
  },
  setTheme(themeVal) {
    Object.keys(themeVal).forEach(k =>
      document.documentElement.style.setProperty(`--${k}`, themeVal[k])
    );
  }
}
