<template>
    <div class="plans-part">
        <center>برنامه ها</center>
        <ul class="plans-part-selection">
            <li><a href="#beginner">مبتدی</a></li>
            <li><a href="#medium">متوسط</a></li>
            <li><a href="#pro">حرفه ای</a></li>
            <li><a href="#skinner">لاغری</a></li>
        </ul>
        <div v-for="(plan, index) in plans" :key="index">
            <div class="part-box" :id="plan.id">
                <div class="head-box">
                    <p>{{plan.name}}</p>
                </div>
                <ul class="ul">
                    <li class="part-box-item" v-for="(item, index) in plan.li" :key="index" ref="drop">
                        <div class="item padding" @click="item.open = !item.open; if(item.sub){dropAct()}">
                                <i v-if="item.sub" class="tran" :class="{'fas fa-sort-up rot': !item.open,'fas fa-sort-up': item.open }"></i>
                                <p>{{item.name}}</p> 
                        </div>
                        <app-dropdown v-if="item.sub" :list="item"></app-dropdown>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import Dropdown from "./plansDropdown.vue";
export default {
        components:{
            appDropdown: Dropdown
        },
        methods:{
            navOpen(){
                const nav = this.$refs.nav.classList
                nav.contains('active') ? nav.replan() : nav.add('active');
            },
            dropAct(){
                const drop = this.$refs.drop.classList
                drop.contains('active') ? drop.replan('active') : drop.add('active');
            }
        },
        computed:{
        plans(){
            return this.$store.getters.plans;
        }
    }
}
</script>
