import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from './../components/Home.vue';
import Login from './../components/profile/Login.vue';
import Profile from './../components/profile/profile.vue';
import Progress from './../components/profile/Progress.vue';
import Stopwatch from './../components/profile/Stopwatch.vue';
import BMI from './../components/profile/BMI.vue';

import Exercise from './../components/exercises/Exercises.vue';
import Plans from './../components/exercises/plans.vue';
import Moves from './../components/exercises/Moves.vue';

Vue.use(VueRouter)

const routes = [
  {path: '/', component: Home},
  {path: '/login', component: Login},
  {path: '/profile', component: Profile, children:[
      {path:'/progress', component: Progress},
      {path:'/stopwatch', component: Stopwatch},
      {path:'/bmi', component: BMI},

  ]},
  {path: '/exercise', component: Exercise ,children:[
      {path: '/moves', component: Moves},
      {path: '/plans', component: Plans}
      ]},
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
