<template>
    <div class="moves-part">
        <center>حرکات</center>
        <ul class="moves-part-selection">
            <li><a href="#chest">سینه</a></li>
            <li><a href="#foot">پا</a></li>
            <li><a href="#hand">بازو</a></li>
            <li><a href="#stomatch">شکم</a></li>
        </ul>
        <div v-for="(move, index) in moves" :key="index">
            <div class="part-box" :id="move.id">
                <div class="head-box">
                    <p>{{move.name}}</p>
                    <img :src="move.img" alt="">
                </div>
                <ul class="ul">
                    <li class="part-box-item" v-for="(item, index) in move.li" :key="index" ref="drop">
                        <div class="item padding" @click="item.open = !item.open; if(item.sub){dropAct()}">
                                <i v-if="item.sub" class="tran" :class="{'fas fa-sort-up rot': !item.open,'fas fa-sort-up': item.open }"></i>
                                <p>{{item.name}}</p> 
                        </div>
                        <app-dropdown v-if="item.sub" :list="item"></app-dropdown>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script>
import Dropdown from "./movesDropdown.vue";
export default {
        components:{
            appDropdown: Dropdown
        },
        methods:{
            navOpen(){
                const nav = this.$refs.nav.classList
                nav.contains("active") ? nav.remove() : nav.add("active");
            },
            dropAct(){
                const drop = this.$refs.drop.classList
                drop.contains("active") ? drop.remove("active") : drop.add("active");
            }
        },
        computed:{
        moves(){
            return this.$store.getters.moves;
        }
    }
}
</script>
