<template>
    <transition
    name="expand"
    @enter= 'enter'
    @after-enter= 'afterEnter'
    @leave= 'leave'>
        <div class="sub-items" v-show="list.open" @click="list.sub.open = !list.sub.open">
            <div class="img-box">
                <img :src="list.sub.img" alt="">
            </div>
            <div class="text-box">
                <div class="text-box-part text-box-description">
                    <p class="text-box-fix">توضیحات:</p><p>{{list.sub.discription}}</p>
                </div>
                <div class="text-box-part text-box-points">
                    <p class="text-box-fix">نکات:</p><p>{{list.sub.points}}</p>
                </div>
                <div class="text-box-part text-box-sets">
                    <p class="text-box-fix">ست ایده آل:</p><p>{{list.sub.sets}}</p>
                </div>
            </div>
        </div>
        
    </transition>
</template>

<script>
export default {
    name: 'app-dropdown',
    props:['list'],
    methods:{
        enter(el){
            el.style.height = 'auto';
            const height = getComputedStyle(el).height;
            el.style.height = 0;
            getComputedStyle(el);
            setTimeout(() => {
                el.style.height = height;
            });
        },
        afterEnter(el){
            el.style.height = 'auto';
        },
        leave(el){
            el.style.height = getComputedStyle(el).height;
            getComputedStyle(el);
            setTimeout(() => {
                el.style.height = 0;
            });
        }
    }
}
</script>

