    .sub-items{
        display: flex;
        flex-direction: column;
        align-items: center;
        position: relative;
        top: 0;
        right: 0;
        width: 100%;
        background-color: var(--div-back2);
        margin-block-start: 0;
        margin-block-end: 0;
        padding-inline-start: 0;
