            border-radius: 4px;
            overflow: hidden;
            margin-bottom: 10px;
            img{
                height: 200px;
            }
        }
        .text-box{
            width: 80%;
            margin: auto;
            font-size: 15px;
            text-align: right;
            .text-box-part{
                display: flex;
